#kullyLogin {
  width: 400px;
  margin: 50px auto;
}
#kullyLogin legend {
  text-align: center;
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 40px;
}
#kullyLogin label {
  display: none;
}
#kullyLogin input {
  width: 100%;
  height: 60px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #333;
  font-size: 16px;
}
#kullyLogin p {
  text-align: right;
}
#kullyLogin p a {
  color: #333;
  text-decoration: none;
}
#kullyLogin .login_btn {
  background: #5F0080;
  color: white;
  border: none;
}
#kullyLogin .join_btn {
  border: 1px solid #5F0080;
  background: white;
}
#kullyLogin .result {
  font-size: 20px;
  font-weight: bold;
}/*# sourceMappingURL=KullyLogin.css.map */