@charset "utf-8";

#kullyLogin {
  width: 400px;
  margin: 50px auto;
  legend {
    text-align: center;
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 40px;
  }
  label {
    display: none;
  }
  input {
    width: 100%;
    height: 60px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #333;
    font-size: 16px;
  }

  p {
    text-align: right;
    a {
      color: #333;
      text-decoration: none;
    }
  }

  .login_btn{
    background: #5F0080;
    color:white;
    border:none;
  }

  .join_btn{
    border: 1px solid #5F0080;
    background: white;
  }

  .result{
    font-size: 20px;
    font-weight: bold;
  }
}
